@import '../variables.scss';
@import '../mediaQuerys.scss';
*{
    font-family: $mainFont;
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
.wrapper{
    width: 100%;
}
.formsPlusChanegeSection{
    min-height: calc(100vh);
    display: flex;
    flex-direction: column;
    @include MQ (tablet-large){
        flex-direction: row;
    }
    .formsContainer__loginForm,
    .formsContainer__registrationForm
    {
        width: 100%;
        flex-shrink: 0;
        display: flex;
        flex-direction: column;
        flex-grow: 3;
        transform: translateX(0);
        transition: transform .7s linear;
         @include MQ (tablet-large){
            width: auto;
         }
    }
    .changeSection{
        flex-grow: 1;
         @include MQ (tablet-large){
            flex-basis: 400px;
            display: flex;
            flex-direction: column;
            justify-content: center;
            margin: 0;
           position: relative;
           .bgImage{
             background-image: url("../../images/changeSectionBG.jpg");
             filter: blur(1px);
             height: 100%;
             background-position: center;
             background-size: cover;
           }
        }
    }
}
.forgotPassword{
    background-image: $mainColorGradient;
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
    height: 100vh;
    z-index: 100;
    display: none;
    transform: translateY(100%);
}
#__react-alert__{
  div>div>div{
      background-color: rgba(0, 4, 31, 0.81) !important;
      width: auto !important;
  }
  span{
    text-transform: lowercase;
    font-size: 16px;
    font-weight: 100;
    letter-spacing: $mainSpacing;
  }
}
.cookieContainer{
    z-index: 999;
    text-align: center;
    position: fixed;
    left: 0;
    right: 0;
    bottom: 0;
    transform: translateY(120%);
    background-color: rgba(50,50,50, .85);
    box-shadow: 0 -2px 16px rgb(50,50,50);
    font-size: 16px;
    letter-spacing: 1.3px;
    transition: 500ms;
}
.cookieContainerActive {
    transform: translateY(0);
}
.emailLink{
    font-style: italic;
    color: rgb(153, 191, 210);
}